<template>
    <div>
        <div data-v-ade07ba4="" class="main">
            <div data-v-27df6730="" data-v-ade07ba4="">
                <div data-v-27df6730="" class="order-list" v-for="(item,index) in revieworders" :key="index">
                    <div data-v-27df6730="" class="ordernum-wrap">
                        <div data-v-27df6730="" class="ordernum">订单编号：{{item.ordernum}}</div>
                        <div data-v-27df6730="" class="status">已收货</div>
                    </div>
                    <div data-v-27df6730="" class="item-list" v-for="(item2,index2) in item.goods" :key="index2">
                        <div data-v-27df6730="" class="image"><img data-v-27df6730=""
                                                                   :src="item2.image"
                                                                   :alt="item2.title"></div>
                        <div data-v-27df6730="" class="title">{{item2.title}}</div>
                        <div data-v-27df6730="" class="amount">x {{item2.amount}}</div>
                        <div data-v-27df6730=""  @click="$router.push('/user/order/add_review?gid='+item2.gid+'&ordernum='+item.ordernum+'')" class="status-btn">{{item2.isreview==='0'?'评价':'追加评价'}}</div>
                    </div>

                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import  {mapActions,mapState} from "vuex"
    import UpRefresh from '../../../assets/js/libs/uprefresh';
    export default {
        mounted(){
            document.title=this.$route.meta.title;
        },
        created(){
            //安全认证
            this.$utils.safeUser(this);
            //创建分页实例
            this.pullUp = new UpRefresh();

            this.getReviewOrder({page:1,success:(pageNum)=>{
                    //分页功能
                    this.pullUp.init({"curPage": 1, "maxPage": parseInt(pageNum), "offsetBottom": 100}, (page) => {
                        this.getReviewOrderPage({status: this.status, page: page});
                    });
                }})
        },
        computed:{
            ...mapState({
                //待评价订单
                revieworders:state=>state.order.revieworders
            })
        },
        beforeDestroy() {
            this.pullUp.uneventSrcoll();
        },
        methods:{
            ...mapActions({
                //待评价定单
                getReviewOrder:"order/getReviewOrder",
                //分页功能
                getReviewOrderPage:"order/getReviewOrderPage"
            })
        }
    }
</script>

<style scoped>
    .order-list {
        width: 100%;
        height: auto;
    }
    .main[data-v-ade07ba4] {
        width: 100%;

        background: #fff;
    }
    .order-list .ordernum-wrap {
        width: 96%;
        height: 0.8rem;
        border-bottom: #EFEFEF 1px solid;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        padding-left: 2%;
        padding-right: 2%;
        align-items: center;
        -webkit-align-items: center;
    }

    .order-list .ordernum-wrap .ordernum, .order-list .ordernum-wrap .status {
        width: auto;
        height: auto;
        font-size: 0.28rem;
    }

    .item-list {
        width: 100%;
        height: 2.4rem;
        border-bottom: #EFEFEF 1px solid;
        position: relative;
        z-index: 1;
    }

    .item-list .image {
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        z-index: 1;
        left: 2%;
        top: 0.2rem;
    }

    .item-list .image img {
        width: 100%;
        height: 100%;
    }

    .item-list .title {
        width: 72%;
        height: auto;
        font-size: 0.28rem;
        position: absolute;
        z-index: 1;
        left: 22%;
        top: 0.2rem;
    }

    .item-list .amount {
        width: auto;
        height: auto;
        position: absolute;
        z-index: 1;
        right: 2%;
        bottom: 0.8rem;
        font-size: 0.24rem;
        color: #909090;
    }

    .item-list .status-btn {
        font-size: 0.24rem;
        width: 1.24rem;
        height: 0.52rem;
        line-height: 0.52rem;
        text-align: center;
        border: 1px solid #f15353;
        border-radius: 4px;
        position: absolute;
        z-index: 1;
        right: 2%;
        bottom: 0.2rem;
    }

    .total-wrap {
        width: 96%;
        height: 0.8rem;
        border-bottom: 1px solid #F17F1F;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        padding-left: 2%;
        padding-right: 2%;
    }

    .total-wrap .total {
        font-size: 0.28rem;
    }
</style>